.ShareView {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden; }
  .ShareView .VideoBox {
    width: 100%;
    height: 5.64rem;
    overflow: hidden;
    position: relative;
    background: #999; }
    .ShareView .VideoBox .video {
      max-width: 100%;
      max-height: 100%;
      z-index: 1; }
    .ShareView .VideoBox::after {
      position: absolute;
      content: '';
      width: 100%;
      height: 100%;
      top: 0px;
      left: 0px;
      opacity: 0.3;
      background-image: var(--backgroundImage);
      background-size: contain; }
  .ShareView .ChildBox {
    width: 100%;
    height: calc(100% - 5.64rem); }
    .ShareView .ChildBox .NavBox {
      width: 100%;
      height: 1.2rem;
      -webkit-box-shadow: #CFCFCF 0 1px 0;
              box-shadow: #CFCFCF 0 1px 0; }
      .ShareView .ChildBox .NavBox a {
        height: 100%; }
      .ShareView .ChildBox .NavBox .NavButton {
        margin: 0 1.06667rem;
        height: 100%;
        font-size: 0.42667rem;
        font-weight: 400;
        color: gray;
        -webkit-transition: -webkit-box-shadow 0.5s;
        transition: -webkit-box-shadow 0.5s;
        -o-transition: box-shadow 0.5s;
        transition: box-shadow 0.5s;
        transition: box-shadow 0.5s, -webkit-box-shadow 0.5s; }
      .ShareView .ChildBox .NavBox .actButton {
        color: #153110;
        -webkit-box-shadow: #153110 0 -0.09333rem 0 inset;
                box-shadow: #153110 0 -0.09333rem 0 inset; }
    .ShareView .ChildBox .ChildRoute {
      width: 100%;
      height: calc(100% - 1.2rem); }



