.Box {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch; }
  .Box .NavBox {
    width: 100%;
    height: 1.26667rem;
    background: #D8EAEA; }
    .Box .NavBox a {
      height: 100%; }
    .Box .NavBox .NavButton {
      width: 2rem;
      height: 100%;
      margin: 0 0.4rem;
      font-size: 0.45333rem;
      font-weight: 500;
      color: #153110; }
    .Box .NavBox .act {
      position: relative; }
      .Box .NavBox .act::after {
        position: absolute;
        content: '';
        width: 0px;
        height: 0px;
        bottom: 0px;
        left: 50%;
        -webkit-transform: translateX(-50%);
            -ms-transform: translateX(-50%);
                transform: translateX(-50%);
        border-top: transparent solid 0px;
        border-bottom: #fff solid 0.2rem;
        border-left: transparent solid 0.18667rem;
        border-right: transparent solid 0.18667rem; }



