.BookView {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden; }
  .BookView .BookInfoTitle {
    width: 100%;
    height: 4.13333rem;
    background: #D8EAEA;
    position: relative; }
    .BookView .BookInfoTitle .BookCoverBox {
      width: 3.28rem;
      height: 100%; }
      .BookView .BookInfoTitle .BookCoverBox .Cover {
        width: 2.10667rem;
        height: 3.04rem;
        overflow: hidden; }
        .BookView .BookInfoTitle .BookCoverBox .Cover img {
          width: 100%;
          display: block; }
    .BookView .BookInfoTitle .InfoContent {
      width: calc(100% - 3.28rem);
      height: 100%; }
      .BookView .BookInfoTitle .InfoContent .BookName {
        font-size: 0.45333rem;
        font-weight: 500;
        color: black;
        margin: 1.06667rem 0 0.36rem 0; }
      .BookView .BookInfoTitle .InfoContent .BookAuthor {
        font-size: 0.34667rem;
        font-weight: 300;
        color: black; }
    .BookView .BookInfoTitle .RankButton {
