      margin: 0.73333rem 0; }
      .BookView .BookIntroduce .IntroduceTitle img:nth-child(1) {
        width: 0.56rem;
        display: block; }
      .BookView .BookIntroduce .IntroduceTitle span:nth-child(2) {
        margin: 0 0.30667rem;
        font-size: 0.45333rem;
        font-weight: 500;
        color: #153110; }
    .BookView .BookIntroduce .Content {
      width: 100%;
      font-size: 0.34667rem;
      font-weight: 300;
      color: #333333;
      line-height: 0.73333rem; }
    .BookView .BookIntroduce .ButtonGroup {
      width: 100%;
      margin: 1.13333rem 0; }
      .BookView .BookIntroduce .ButtonGroup .Button {
        width: 3.38667rem;
        height: 1.06667rem;
        background: #153110;
        border-radius: 0.53333rem;
        font-size: 0.45333rem;
        font-weight: 500;
        color: #acdcda;
        margin: 0 0.42667rem; }
  .BookView .Loading {
    width: 100%;
    height: 100%; }
    .BookView .Loading img {
      width: 4rem; }



